import React, { Component } from 'react';
import { Link } from "react-router-dom";

class NavItems extends Component {
  render() {
    return (
      <li id={this.props.text}>
        <Link to={this.props.tolink} onClick={this.props.activeItem.bind(this, this.props.text)}>{this.props.text}</Link>
      </li>
    )
  }
}

export default NavItems
