import React, { Component } from 'react'
import NavItems from './NavItems'

class Navbar extends Component {
  constructor(props) {
    super(props)
  
    this.state = {
       'ActiveItem': ''
    }
  }

  activeItem = x => {
    if (this.state.ActiveItem !== '') {
      document.getElementById(this.state.ActiveItem).classList.remove('active');
    }

    this.setState({'ActiveItem': x}, () => {
      document.getElementById(this.state.ActiveItem).classList.add('active');
    })
  }
  

  render() {
    return (
      <nav>
        <ul>
          <NavItems text="Home" tolink="/" activeItem={this.activeItem}/>
          <NavItems text="About" tolink="/about" activeItem={this.activeItem}/>
          <NavItems text="Education" tolink="/education" activeItem={this.activeItem}/>
          <NavItems text="Skills" tolink="/skills" activeItem={this.activeItem}/>
          <NavItems text="Contact" tolink="/contact" activeItem={this.activeItem}/>
        </ul>
      </nav>
    )
  }
}

export default Navbar
